
import * as types from '../mutation-types';

import { __ENV__ } from '../../../env';

export const getResumeUrl = () => { return __ENV__.resumeUrl || './assets/json/resume.json'; }

export const beforeGetResume = (store) => {
    let {commit} = store;
    commit(types.BEFORE_GET_RESUME);
};

export const getResume = ({commit,dispatch}) => {
    
    dispatch('beforeGetResume');
    let xhr = new XMLHttpRequest();
    xhr.onreadystatechange = () => {
        if(xhr.readyState === 4) {
            if(xhr.status >= 200 && xhr.status < 300) {
                commit({
                    type:types.GET_RESUME,
                    resume:JSON.parse(xhr.responseText)
                });
            } 
            else {
                commit(types.SET_ERROR,
                    'request was unsuccessful: ' + xhr.status
                );
            }
        }
    };
    xhr.open('GET',this.getResumeUrl(),true);
    xhr.send(null);
};
//https://1242035.github.io
export const setResumePage = ({commit}, page) => {
    
    commit({ 
        type:types.SET_RESUME_PAGE,
        page:parseInt(page) 
    });
};

export const setPrint = ({commit}, isPrint) => {
    
    commit({
        type: types.SET_PRINT,
        print:isPrint
    });  
};


